import Vue from 'vue'
import App from './App'
import BootstrapVue from 'bootstrap-vue'
import VeeValidate from 'vee-validate';

Vue.config.productionTip = false
Vue.use(BootstrapVue);
Vue.use(VeeValidate);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { 
    App
  }
});


