// HTML
// ============================================================================
<template>
  <div id="global">

    <!-- Homepage -->
    <div v-show="showHome" class="background">
      <div class="container">
          <div class="row">
              <div class="col-md-12">
                  <a href="https://www.rmit.edu.au/library" class="logo">RMIT University Library</a>
              </div>
          </div>
      </div>
      <div id="home" class="container">
        <div class="row">
          <div class="col-md-12">
            <div id="main">
              <h1 id="title">{{ title }}</h1>
              <hr class="red-line" align="left">
              <p id="preamble" v-html="preamble"></p>
              <div class="course-dropdown-container col-centered">
                <b-form-select v-model="select" class="course-dropdown">
                  <option :value="null" selected disabled>I am studying...</option>
                  <option :value="option.value" v-for="option in options" v-bind:key="option">{{ option.text }}</option>
                </b-form-select>
                <button class="go-btn btn" size="lg" v-on:click="getQuiz">Go</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Quiz -->
    <transition name="fade" mode="in-out">
      <div v-show="showNav">
        <navigation v-bind:field="field"></navigation>
      </div>
    </transition>
    <transition name="fade">
      <div v-show="showQuiz" class="container">
        <div class="row">
          <div class="col-md-12">
            <div id="quiz-window">
              <div v-for="(question, index) in quiz.questions" :key="index">
                <div class="question-box" v-show="index === questionIndex">
                  <p class="question-statement">{{ question.statement }}</p>
                  <ol class="answers">
                    <li v-for="(answer, answerIndex) in question.answers" v-bind:key="answer">
                      <input v-bind:name="question.statement"
                             v-bind:id="index + '-' + answerIndex"
                             v-bind:value="answerIndex + 1"
                             v-model="score[index]"
                             type="radio">
                      <label v-bind:for="index + '-' + answerIndex"
                             v-on:click="onOptionSelect(answer, index)"
                             class="state-radio">
                        {{ answer }}
                      </label>
                    </li>
                  </ol>
                  <div class="navigation-button-container col-centered">
                    <b-button class="navigation-button previous-btn"
                              size="lg"
                              :disabled="questionIndex == 0"
                              v-on:click="previous">Previous</b-button>
                    <b-button class="navigation-button next-btn" 
                              size="lg"
                              :disabled="!questionAnswer[questionIndex]"
                              v-if="questionIndex !== quiz.questions.length - 1"
                              v-on:click="next">Next</b-button>
                    <b-button class="navigation-button"
                              size="lg"
                              :disabled="!questionAnswer[questionIndex]"
                              v-if="questionIndex === quiz.questions.length - 1"
                              v-on:click="resultsHandler">See my results</b-button>
                    <div id="question-number">
                      {{ questionIndex + 1 }} / {{ quiz.questions.length }}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>

    <!-- Results -->
    <div v-show="showResults" class="container">
      <div class="row">
        <div class="col-md-8 col-centered">
          <div id="results-container">
            <img src="../assets/img/medal.svg" class="medal">
            <h2>Results</h2>
            <hr>
            <div v-if="totalScore <= 0.25">
                <div class="star-container">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-hollow.svg">
                <img class="star" src="../assets/img/star-hollow.svg">
                <img class="star" src="../assets/img/star-hollow.svg">
              </div>
              <p>You scored 1 out of 4 stars!</p>
              <div class="feedback">
                To realistically perform well in {{ field }}, you will need 
                to not only strengthen your maths skills but also increase 
                your maths confidence.
              </div>
            </div>
            <div v-else-if="totalScore > 0.25 && totalScore <= 0.5">
              <div class="star-container">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-hollow.svg">
                <img class="star" src="../assets/img/star-hollow.svg">
              </div>
              <p>You scored 2 out of 4 stars!</p>
              <div class="feedback">
                You will need to make good use of the available maths support 
                and resources.
              </div>
            </div>
            <div v-else-if="totalScore > 0.5 && totalScore <= 0.75">
              <div class="star-container">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-hollow.svg">
              </div>
              <p>You scored 3 out of 4 stars!</p>
              <div class="feedback">
                You will find the maths support and resources helpful to refresh 
                your memory and sharpen your skills.
              </div>
            </div>
            <div v-else>
              <div class="star-container">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-filled.svg">
                <img class="star" src="../assets/img/star-filled.svg">
              </div>
              <p>You scored 4 out of 4 stars!</p>
              <div class="feedback">
                Your maths background and confidence level are strong enough 
                for you to start a degree in {{ field }} with confidence.
              </div>
            </div>

            <!-- Send me a copy of my responses -->
            <div id="results-button-container">
                <label for="email-input">Send me a copy of my responses</label>
                <p class="note"><strong>Please note:</strong> The format of your RMIT student email is <span class="email">s1234567@student.rmit.edu.au</span></p>
                <b-input-group id="email-results-input">
                  <input id="email-input"
                         v-validate="'email'"
                         v-model="email"
                         v-bind:class="{'input': true, 'is-danger': errors.has('email') }"
                         class="form-control"
                         name="email" type="text"
                         placeholder="e.g. s1234567@student.rmit.edu.au">
                  <div id="submit-button-container">
                  <b-input-group-button slot="right">
                    <b-btn v-on:click="onEmailResults($event)" class="submit-email-btn btn-lg">Submit</b-btn>
                  </b-input-group-button>
                  </div>
                </b-input-group>
                <div v-show="errors.has('email')" class="alert-email">{{ errors.first('email') }}</div>

                <!-- Dismissible alert -->
                <transition name="fade">
                  <b-alert variant="success"
                          dismissible
                          :show="clickedSubmit"
                          @dismissed="clickedSubmit=false">
                    Your responses have been sent to <strong>{{ emailSaved }}</strong>.
                  </b-alert>
                </transition>
            </div>

            <!-- Resources -->
            <img src="../assets/img/open-book.svg" class="book">
            <h3>Resources</h3>
            <hr>
            <ul id="resources">
              <li v-for="link in links" v-bind:key="link">
                <a v-bind:href="link.url">{{ link.title }}</a><br>
                <p class="resource-description">{{ link.description }}</p>
              </li>
            </ul>

          </div>
        </div>
      </div>

      <!-- Footer -->
      <div id="footer">
        Copyright &copy; {{ new Date().getFullYear() }} RMIT <span class="middot">&middot;</span> Developed by the University Library &amp; Student Success
      </div>

    </div>
  </div>
</template>

// JavaScript
// ============================================================================
<script>
import navigation from './Navigation';
import json from '../json/data.json';
import axios from 'axios';
import querystring from 'querystring';

export default {
  name: 'quiz',
  components: {
    navigation
  },
  data() {
    return {
      json: json,
      title: 'Getting started: Maths',
      preamble: 'The Getting started: Maths quiz is a short self-assessment tool designed \
        to help you transition into tertiary mathematics at RMIT.<br><br>To \
        begin, select your field of study and click Go.',
      options: [
        { value: 0, text: 'Business' },
        { value: 1, text: 'Engineering' },
        { value: 2, text: 'Health Science' }
      ],
      links: [
        {
          title: 'Drop-in learning centres',
          description: 'The SLC drop-in learning centres are free to all RMIT students. No appointments are necessary.',

          url: 'https://www.rmit.edu.au/students/study-support/study-and-learning-centre/drop-in'
        },
        {
          title: 'Maths and science study resources',
          description: 'Visit the Study and Learning Centre\'s free drop-in service to help improve your skills.',

          url: 'https://www.rmit.edu.au/students/study-support/study-and-learning-centre/maths-and-science'
        },
        {
          title: 'Learning Lab',
          description: 'The RMIT Learning Lab gives you access to a wide range of online resources to help you improve your general academic study, writing and maths, statistics, physics and chemistry skills.',
          url: 'https://emedia.rmit.edu.au/learninglab'
        },
        {
          title: 'Study KnowHow workshops',
          description: 'Develop your skills by attending one or more of our workshop series. All RMIT students are welcome.',
          url: 'https://www.rmit.edu.au/students/study-support/study-and-learning-centre/workshops'
        }
      ],
      select: null,
      field: null,
      quiz: {},
      questionIndex: 0,
      questionAnswer: null,
      optionSelected: false,
      score: null,
      totalScore: 0,
      showHome: true,
      showNav: false,
      showQuiz: false,
      showResults: false,
      showEmailDropdrown: false,
      clickedSubmit: false,
      email: '',
      emailSaved: ''
    }
  },
  methods: {
    getQuiz: function() {
      this.quiz = json[this.select];
      this.field = json[this.select].course;
      this.score = Array(this.quiz.questions.length).fill(0);
      this.questionAnswer = Array(this.quiz.questions.length).fill(null);
      this.showHome = false;
      this.showNav = true;
      this.showQuiz = true;
    },
    onOptionSelect: function(answer, index) {
      this.optionSelected = true;
      this.questionAnswer[index] = answer;
    },
    next: function() {
      this.questionIndex++;
      this.optionSelected = false;
    },
    previous: function() {
      this.questionIndex--;
    },
    resultsHandler: function() {
      this.getResults();
      this.submitToGoogle();
    },
    getResults: function() {
      const maxScore = 4 * this.quiz.questions.length;
      for (var index in this.score) {
        if (this.score[index] != 0) {
          this.totalScore += this.score[index] - 1;
        }
      }
      this.totalScore = this.totalScore / maxScore;
      this.showQuiz = false;
      this.showResults = true;
    },
    submitToGoogle: function() {
      const url = 'https://docs.google.com/forms/d/e/1FAIpQLSfihEkQRl4vhgYcnXbul0gWiFWSar8XJ5cPa6zg5afwUiveLQ/formResponse';
      let entries = [];
      if (this.field === 'Business') {
        entries = [
          'entry.1967278508',
          'entry.1285179105',
          'entry.789619582',
          'entry.1884996314',
          'entry.551222742',
          'entry.1457607474',
          'entry.1234323250',
          'entry.1178503533',
          'entry.1736812019',
          'entry.725097439',
        ];
      } else if (this.field === 'Engineering') {
        entries = [
          'entry.1298041166',
          'entry.1816623916',
          'entry.2133169674',
          'entry.1829948873',
          'entry.128477790',
          'entry.1110011476',
          'entry.708431470',
          'entry.1066332612',
          'entry.1684293107',
          'entry.140343093',
        ];
      } else if (this.field === 'Health Science') {
        entries = [
          'entry.1975698717',
          'entry.1930157857',
          'entry.1181591756',
          'entry.1094299205',
          'entry.1813188419',
          'entry.495704007',
          'entry.191501564',
          'entry.1003895610',
          'entry.133027678',
          'entry.516483681',
          'entry.737203243',
          'entry.1284342068',
          'entry.1698823788',
          'entry.205109974'
        ];
      }
      let formDataForGoogle = {};
      formDataForGoogle['entry.689738448'] = this.field;
      formDataForGoogle['entry.929666511'] = this.totalScore;
      for (var index in entries) {
          formDataForGoogle[entries[index]] = this.questionAnswer[index];
      }
      axios.post(url, querystring.stringify(formDataForGoogle))
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error.message);
        });
    },
    onEmailResults: function(event) {
      const url = 'https://docs.google.com/forms/d/e/1FAIpQLSeHJ21qcbyw_21q8aXWDgvl18X_cOGbEI8O903AKTZrSX4jbA/formResponse';
      const questionAnswerStr = this.questionAnswer.join(';');
      const questionArray = [];
      for (var i in this.quiz.questions) {
        questionArray.push(this.quiz.questions[i].statement);
      }
      const questionArrayStr = questionArray.join(';');
      const formDataForGoogle = {
        'entry.362559713': this.email,
        'entry.1181118177': this.field,
        'entry.2065573821': questionArrayStr,
        'entry.4241845': questionAnswerStr,
        'entry.1271292008': this.totalScore
      };
      axios.post(url, querystring.stringify(formDataForGoogle))
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error.message);
        })
      if (!this.errors.has('email') && this.email != '') {
        this.clickedSubmit = true;
        this.emailSaved = this.email;
        this.email = '';
      }
    }
  },
  watch: {
    email: function() {
      if (this.errors.has('email') && this.clickedSubmit) {
        this.clickedSubmit = false;
      }
    }
  }
};
</script>

// SCSS
// ============================================================================
<style lang="scss" scoped>

$rmitred: #DC291E;
$rmitblue: #000053;
$white: #FFFFFF;
