  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  background: url("../assets/img/background.jpg");
  background-size: 100%;
  background-position: center;
}

.logo {
  float: right;
  margin-top: 40px;
  width: 175px;
  height: 62px;
  text-indent: -9999px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../assets/img/rmit-logo.png");
}

.col-centered {
  float: none;
  margin: 0 auto;
  text-align: center;
}

/* Type */
