  font-family: "Museo300";
  font-size: 90px;
  color: $rmitblue;
}

p {
  font-family: "Museo300";
  font-size: 22px;
  color: $rmitblue;
}

p#preamble {
  width: 75%;
}

h3 {
  color: $rmitblue;
}

p.question-statement {
  margin: 50px 0;
  font-family: "Museo300";
  font-size: 24px;
  color: $rmitblue;
  text-align: center;
}

hr.red-line {
  margin: 20px 0 30px 0;
  width: 80px;
  border-color: $rmitred;
  border-width: medium;
}

/* Dropdown */
.course-dropdown-container {
  margin: 60px auto;
}

.course-dropdown {
  display: inline-block;
  height: 40px !important;
  max-width: 375px;
  padding: 0 15px;
  border-radius: $border-radius;
  font-family: "Museo300";
  font-size: 18px;
}

.go-btn {
  width: 70px;
  border-radius: $border-radius;
  background-color: $rmitred;
  font-family: "Museo300";
  font-weight: bold;
  color: $white;
}

.go-btn:hover {
  background-color: lighten($rmitred, 10%);
}

/* Quiz */
ol.answers {
  padding-left: 0;
}

ol.answers li {
  list-style-type: none;
}

#question-number {
  margin: 15px;
  font-family: "Museo300";
  color: $rmitblue;
}

/* Quiz buttons */
.state-radio {
    display: block;
    margin: 10px auto;
    max-width: 600px;
    border-radius: 5px;
    padding: 20px;
    background-color: $lightblue;
    transition: all .3s;
    cursor: pointer;
    font-size: 20px;
    font-family: "Museo300";
    text-align: left;
}

.state-radio:hover {
  background-color: rgba(217, 217, 228, 0.7);
}

input[type="radio"] {
  display: none;
}

input[type="radio"]:checked + label {
  color: $white;
  background-color: $rmitblue;
}

/* Navigation buttons */
.navigation-button-container {
  margin: 50px 0;
}

.navigation-button {
  width: 200px;
  margin: 5px;
  border: none;
  border-radius: $border-radius;
  background-color: $rmitred;
  font-family: "Museo300";
  font-weight: bold;
  color: $white;
}

.navigation-button:hover {
  background-color: lighten($rmitred, 10%);
}

.navigation-button:disabled,
.navigation-button:disabled:hover {
  background-color: darken($lightgrey, 15%);
}

/* Results */
#results-container {
  margin-top: 50px;
  text-align: center;
}

#results-container h2,
#results-container h3 {
  font-family: "Museo300";
  color: $rmitblue;
}

#results-container hr {
  margin: 20px auto;
  width: 100%;
  border-color: $lightgrey;
  border-width: thin;
}

#results-container img.medal,
#results-container img.book {
  max-width: 70px;
  margin-bottom: 10px;
}

#results-container img.book {
  max-width: 60px;
  margin-top: 40px;
}

#results-container .star-container {
    margin: 35px 0 10px 0;
}

#results-container p {
  font-size: 22px;
}

#results-container p.note {
  font-size: 16px;
  margin-bottom: 15px;
  font-family: 'Helvetica', Arial, sans-serif;
  color: #333;
}

#results-container .star {
  max-width: 30px;
}

#results-container .feedback {
  padding-left: 40px;
  padding-right: 40px;
  text-align: left;
  margin-bottom: 50px;
}

#results-container #resources {
  text-align: left !important;
}

#results-container #resources a,
#results-container #resources a:active,
#results-container #resources a:visited {
  color: $rmitred;
  text-decoration: underline;
}

#results-container #resources a:hover {
  color: $rmitblue;
  text-decoration: underline;
}

#results-container #resources .resource-description {
  margin-left: 18px;
  font-family: "Helvetica Neue", Arial, Helvetica, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.3;
  color: #212529;
}

#results-container ul {
	list-style: none;
}

#results-container ul li {
  margin: 10px 0;
}

#results-container ul li:before {
  content: "\279C \0020";
  color: $rmitblue;
}

