  border-radius: 0 25px 25px 0;
  padding: 7px 20px;
  font-family: "Museo300", "Helvetica Neue", Arial, Helvetica, sans-serif;
}

#results-button-container .alert-email {
  font-size: 14px;
  color: $rmitred;
}

#results-button-container .alert-dismissible {
  margin: 10px auto;
  width: 80%;
  font-size: 15px;
}

/* Footer */
#footer {
  margin-top: 75px;
  padding: 30px;
  border-top: 1px solid $lightgrey;
  font-size: 14px;
  color: $rmitblue;
  text-align: center;
}

#footer span.middot {
  margin: 0 5px;
}

/* Transitions */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.75s;
}

.fade-enter,
.fade-leave-to {
