  }

  #results-button-container .submit-email-btn {
    display: block;
    border-radius: 25px;
    font-size: 14px;
    padding: 10px 30px;
  }

  #submit-button-container {
    margin: 0 auto;
  }

  /* Resources */
  ul#resources {
    padding-left: 0;
  }

}

</style>



